@charset 'utf-8';
@import '_common';

#mf-content {
  h1 {
    color: $theme-color-text-primary;
    margin-bottom: 20px;
    font-size: 32px;
    font-family: $theme-font-code;
    padding: 0 15px;
    transition: margin-top .3s ease-out, font-size .3s ease-out;

    @media (max-width: $break-point-tablet) {
      font-size: 28px;
    }

    &:first-of-type {
      margin-top: 50px;

      @media (max-width: $break-point-tablet) {
        margin-top: 20px;
      }
    }
  }

  .category {
    @include clearfix;
    margin-bottom: 20px;
    width: 100%;
    padding: 0 15px;

    .item {
      display: block;
      float: left;
      width: calc((100% - 20px) / 3);
      padding: 50px 10px;
      margin-right: 10px;
      margin-bottom: 10px;
      border: 1px solid $theme-color-text-secondary;
      color: $theme-color-text-secondary;
      text-align: center;
      font-family: $theme-font-code;
      transition: color .3s ease-out, border-color .3s ease-out;
      @include ellipsis;

      @media (max-width: 480px) {
        width: calc((100% - 10px) / 2);
      }

      &:hover {
        color: $theme-color-text-primary;
        border-color: $theme-color-text-primary;
      }

      &:nth-child(3n+3) {
        margin-right: 0;

        @media (max-width: 480px) {
          margin-right: 10px;
        }
      }

      &:nth-child(even) {
        @media (max-width: 480px) {
          margin-right: 0;
        }
      }
    }
  }
}